@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
.container{
    background-image: url(/react-gh-pages/static/media/background1.2bf56e96.jpg);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    top: 5;
    bottom: 10;
    left: 0;
    right: 0;
    height:160vh;

}

.container .content-container{
    background-color:rgba(221, 221, 221, 0.5);

    font-size: 20px;
    font-weight: 700;
    text-align: left;
}



.container .image-info-container{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content:left;
            justify-content:left;
    -webkit-flex-wrap: wrap-reverse;
            flex-wrap: wrap-reverse;
    -webkit-align-items:center;
            align-items:center;
    margin: 0vh 0vh 0vh 15vh
}

.container .image-container{
    height: 30vh;
    max-width: 30vh;
}


.container .info-container{
    margin-left: 10vh;
    -webkit-justify-content:left;
            justify-content:left;
}



.container .text-container{

    width: 100vh;
    -webkit-justify-content:flex-start;
            justify-content:flex-start;
    -webkit-align-items:flex-end;
            align-items:flex-end;

}

.container .row{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content:left;
            justify-content:left;
    -webkit-align-items:center;
            align-items:center;
}


.education-container{

    margin-left: 15vh;
    width: 80vh


}



.education-container .array-object{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content:left;
            justify-content:left;
    -webkit-flex-wrap: wrap-reverse;
            flex-wrap: wrap-reverse;
    -webkit-align-items:center;
            align-items:center;
}

.education-container a{
    margin-right: 2vh;
}


.contact-me-container{
    margin: 10vh;
    margin-left: 15vh;

    float: right;
    max-width: 600px;
    width: 45vh;
    padding: 5vh 3vh;
    background: rgba(255, 255, 255, 0.836);
    box-shadow: 2px 2px 5px #666;
    -webkit-align-items: center;
            align-items: center;
}

.login_page .text-field
{
    width: 38vh;
}

.contact-me-container input{
    width: 90%;
    height: 45px;
    margin: 10px 0;
    border: 2px solid #777;
    outline: none;
    padding: 0 10px;
}

.contact-me-container  button{
    width: 150px;
    padding: 10px 0;
    color: white;
    background: black;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    margin-right: 10px;
    margin-top: 10px;
}







/* base styles */
* {  
  font-family: "Quicksand";
  background-image: 'url("/online-marketplace/public/img/background.jpg")';
  
}

body{
  margin: 0;
}

